@use "sass:map";
@import "../utils/variables";

.todoapp {
  background: (map.get($colors, "app-bg"));
  margin: 130px 0 40px;
  position: relative;
  box-shadow: 0 2px 4px 0 (map.get($shadows, "app-close")),
    0 25px 50px 0 (map.get($shadows, "app-long"));

  &__header {
    position: relative;
  }

  &__title {
    position: absolute;
    top: -155px;
    width: 100%;
    font-size: 100px;
    font-weight: 100;
    text-align: center;
    color: (map.get($colors, "title"));
    -webkit-text-rendering: optimizeLegibility;
    -moz-text-rendering: optimizeLegibility;
    text-rendering: optimizeLegibility;
  }

  &__toggle-all-label {
    position: absolute;

    height: 100%;
    width: 45px;
    z-index: 2;

    display: flex;
    justify-content: center;
    align-items: center;

    font-size: $typing-font-size;
    color: (map.get($colors, "main-light"));

    border: 0;
    background-color: transparent;
    cursor: pointer;

    &.active {
      color: (map.get($colors, "toggleAll-active"));
      transition: color 0.4s;
    }

    &:before {
      content: "❯";
      position: relative;
      left: calc($typing-font-size / 2);
      transform: rotate(90deg);
      line-height: 0;
      cursor: pointer;
    }

    &--hidden {
      visibility: hidden;
    }
  }

  &__toggle-all {
    opacity: 0;
    cursor: pointer;

    @media screen and (-webkit-min-device-pixel-ratio:0) {
      background: none;
    }
  }

  &__new-todo {
    position: relative;
    margin: 0;
    width: 100%;
    font-size: $typing-font-size;
    font-family: inherit;
    font-weight: inherit;
    line-height: 1.4em;
    color: inherit;
    padding: 16px 16px 16px 60px;
    border: none;
    background: (map.get($colors, "editForm-bg"));
    box-shadow: inset 0 -2px 1px (map.get($shadows, "editForm-box"));
    box-sizing: border-box;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    &::placeholder {
      font-style: italic;
      font-weight: 300;
      color: (map.get($colors, "main-light"));
    }

    &::-ms-input-placeholder,
    &::-moz-placeholder,
    &::-webkit-input-placeholder {
      font-style: italic;
      font-weight: 300;
      color: (map.get($colors, "main-light"));
    }
  }

  &__main {
    position: relative;
    z-index: 2;
    border-top: 1px solid (map.get($colors, "main-light"));
  }

  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
  }

  &__footer {
    display: flex;
    justify-content: center;
    align-items: center;
    color: map.get($colors, "footer");
    padding: 10px 15px;
    height: 20px;
    text-align: center;
    border-top: 1px solid (map.get($colors, "main-light"));

    &::before {
      content: "";
      position: absolute;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: -1;
      height: 50px;
      overflow: hidden;
      box-shadow: 0 1px 1px (map.get($shadows, "app-close")),
        0 8px 0 -3px (map.get($shadows, "app-light")),
        0 9px 1px -3px (map.get($shadows, "app-close")),
        0 16px 0 -6px (map.get($shadows, "app-light")),
        0 17px 2px -6px (map.get($shadows, "app-close"));
    }

    @media (max-width: 430px) {
      height: 50px;
    }
  }

  &__todo-count {
    position: absolute;
    left: 2%;
  }

  &__clear-completed {
    position: absolute;
    right: 2%;

    margin: 0;
    padding: 0;
    border: 0;

    font-family: inherit;
    font-weight: inherit;
    color: inherit;
    text-decoration: none;

    cursor: pointer;
    background: none;

    -webkit-appearance: none;
    appearance: none;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    &:hover {
      text-decoration: underline;
    }

    &:active {
      text-decoration: none;
    }
  }
}
