e:none}.todoapp__footer{display:flex;justify-content:center;align-items:center;color:#777;padding:10px 15px;height:20px;text-align:center;border-top:1px solid #e6e6e6}.todoapp__footer::before{content:"";position:absolute;right:0;bottom:0;left:0;z-index:-1;height:50px;overflow:hidden;box-shadow:0 1px 1px rgba(0,0,0,.2),0 8px 0 -3px #f6f6f6,0 9px 1px -3px rgba(0,0,0,.2),0 16px 0 -6px #f6f6f6,0 17px 2px -6px rgba(0,0,0,.2)}@media(max-width: 430px){.todoapp__footer{height:50px}}.todoapp__todo-count{position:absolute;left:2%}.todoapp__clear-completed{position:absolute;right:2%;margin:0;padding:0;border:0;font-family:inherit;font-weight:inherit;color:inherit;text-decoration:none;cursor:pointer;background:none;-webkit-appearance:none;appearance:none;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.todoapp__clear-completed:hover{text-decoration:underline}.todoapp__clear-completed:active{text-decoration:none}.todo{position:relative;display:grid;grid-template-columns:45px 1fr;justify-items:stretch;font-size:24px;line-height:1.4em;border-bottom:1px solid #ededed}.todo:last-child{border-bottom:none}.todo__toggle-label{cursor:pointer;background-image:url("data:image/svg+xml;utf8,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20width%3D%2240%22%20height%3D%2240%22%20viewBox%3D%22-10%20-18%20100%20135%22%3E%3Ccircle%20cx%3D%2250%22%20cy%3D%2250%22%20r%3D%2250%22%20fill%3D%22none%22%20stroke%3D%22%23ededed%22%20stroke-width%3D%223%22/%3E%3C/svg%3E");background-repeat:no-repeat;background-position:center left}.todo.completed .todo__toggle-label{background-image:url("data:image/svg+xml;utf8,%3Csvg%20xmlns%3D%22http%3A//www.w3.org/2000/svg%22%20width%3D%2240%22%20height%3D%2240%22%20viewBox%3D%22-10%20-18%20100%20135%22%3E%3Ccircle%20cx%3D%2250%22%20cy%3D%2250%22%20r%3D%2250%22%20fill%3D%22none%22%20stroke%3D%22%23bddad5%22%20stroke-width%3D%223%22/%3E%3Cpath%20fill%3D%22%235dc2af%22%20d%3D%22M72%2025L42%2071%2027%2056l-4%204%2020%2020%2034-52z%22/%3E%3C/svg%